:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.logo:hover {
  border: var(--blue-color) solid 2px;
  text-decoration: none;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-flex;
}

.nav__item {
  text-transform: uppercase;
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link {
  color: #000;
  text-align: center;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active, .nav__link:hover {
  color: var(--blue-color);
}

